@import url('https://fonts.googleapis.com/css?family=Exo:400,700');

.app {
    display: flex;
    justify-content: center;
    align-items: center;
}

.qcontainer {
    display: flex;
    flex-direction: column;
    justify-content: center;
    width: 80vh;
    height: 80vh;
}

.qbox {
    display: flex;
    justify-content: center;
    text-align: center;
    text-wrap: wrap;
    flex-direction: column;
    color: white;
}

.ansbox {
    display: flex;
    justify-content: center;
    align-self: center;
    width: 80%;
    flex-direction: column;
    gap: 20px;
    color: blue;
    padding: 20px;
}

/* CSS */
.button {
    z-index: 999;
    background-image: linear-gradient(92.88deg, #45b5a2 9.16%, #43ccb5 43.89%, #3fd7a9 64.72%);
    border-radius: 8px;
    border-style: none;
    box-sizing: border-box;
    color: #FFFFFF;
    cursor: pointer;
    flex-shrink: 1;
    font-family: "Inter UI", "SF Pro Display", -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen, Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
    font-size: 20px;
    font-weight: 500;
    height: 4rem;
    padding: 0 1.6rem;
    text-align: center;
    text-shadow: rgba(0, 0, 0, 0.25) 0 3px 8px;
    transition: all .5s;
    user-select: none;
    -webkit-user-select: none;
    touch-action: manipulation;
    opacity: 80%;
}

.button p {
    opacity: 100%;
}

.button:hover, .button:active, .button:focus {
    background-image: linear-gradient(92.88deg, #45b5a2 49.16%, #43ccb5 83.89%, #3fd7a9 104.72%);
    height: 4.3rem;
    transition-duration: .2s;
}

@media (min-width: 768px) {
    .button {
        padding: 0 2.6rem;
    }
}

.resultbox {
    display: flex;
    justify-content: center;
    text-align: center;
    text-wrap: wrap;
    flex-direction: column;
    color: white;    
    animation: 1s fadeIn;
    font-size: 20px;
    transition: all 1s;
}

.correct {
    color: rgb(0, 255, 213);
    font-size: 40px;
    font-weight: 700;
    animation: 1s fadeIn;
    transition: all 1s;
}

.incorrect {
    color: rgb(40, 53, 51);
    font-size: 40px;
    font-weight: 700;
    animation: 1s fadeIn;
    transition: all 1s;

}


* {
    margin: 0px;
    padding: 0px;
    font-family: 'Exo', sans-serif;
}

.area {
    background: #47a37b;
    background: -webkit-linear-gradient(to left, #8f94fb, #4e54c8);
    width: 100%;
    height: 100vh;


}

.circles {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    overflow: hidden;
}

.circles li {
    position: absolute;
    display: block;
    list-style: none;
    width: 20px;
    height: 20px;
    border-radius: 100%;
    background: rgb(255, 255, 255);
    animation: animate 25s linear infinite;
    bottom: -150px;

}

.circles li:nth-child(1) {
    left: 25%;
    width: 80px;
    height: 80px;
    animation-delay: 0s;
}


.circles li:nth-child(2) {
    left: 10%;
    width: 20px;
    height: 20px;
    animation-delay: 2s;
    animation-duration: 12s;
}

.circles li:nth-child(3) {
    left: 70%;
    width: 20px;
    height: 20px;
    animation-delay: 4s;
}

.circles li:nth-child(4) {
    left: 40%;
    width: 60px;
    height: 60px;
    animation-delay: 0s;
    animation-duration: 18s;
}

.circles li:nth-child(5) {
    left: 65%;
    width: 20px;
    height: 20px;
    animation-delay: 0s;
}

.circles li:nth-child(6) {
    left: 75%;
    width: 110px;
    height: 110px;
    animation-delay: 3s;
}

.circles li:nth-child(7) {
    left: 35%;
    width: 150px;
    height: 150px;
    animation-delay: 7s;
}

.circles li:nth-child(8) {
    left: 50%;
    width: 25px;
    height: 25px;
    animation-delay: 15s;
    animation-duration: 45s;
}

.circles li:nth-child(9) {
    left: 20%;
    width: 15px;
    height: 15px;
    animation-delay: 2s;
    animation-duration: 35s;
}

.circles li:nth-child(10) {
    left: 85%;
    width: 150px;
    height: 150px;
    animation-delay: 0s;
    animation-duration: 11s;
}



@keyframes animate {

    0% {
        transform: translateY(0) rotate(0deg);
        opacity: 1;
    }

    100% {
        transform: translateY(-1000px) rotate(720deg);
        opacity: 0;
    }

}